.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

header {
  color: #fff;
  z-index: 999;
  background-color: #007bff;
  padding: 20px 0;
  position: sticky;
  top: 0;
}

.header__title {
  margin: 0;
  font-size: 32px;
  font-weight: bold;
}

.header__form {
  max-width: 500px;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header__form-input {
  border: none;
  border-radius: 5px;
  flex-grow: 1;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.header__form-input::-ms-input-placeholder {
  color: #6c757d;
}

.header__form-input::placeholder {
  color: #6c757d;
}

.header__form-btn {
  color: #6c757d;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  transition: all .2s ease-in-out;
}

.header__form-btn:hover {
  color: #fff;
  background-color: #6c757d;
}

.header__form-btn:focus {
  outline: none;
}

.header__form-icon {
  font-size: 24px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  margin: 30px 0;
  display: grid;
}

.photo-card {
  cursor: pointer;
  border-radius: 5px;
  flex-direction: column;
  transition: all .2s ease-in-out;
  display: flex;
  position: relative;
  overflow: hidden;
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info {
  color: #fff;
  width: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, .5);
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  transition: all .2s ease-in-out;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.info p {
  margin: 0;
  font-size: 14px;
}

.photo-card:hover .info {
  opacity: 1;
}

.info-item:last-child {
  margin-bottom: 0;
}

.info-item b {
  font-weight: bold;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 30px auto 0;
  padding: 10px 20px;
  transition: all .2s ease-in-out;
  display: block;
}

.load-more:hover {
  background-color: #0062cc;
}

.load-more:focus {
  outline: none;
}

.is-hidden {
  display: none;
}

